"use strict";

//Variabler
const url = "https://dahlgren.miun.se/ramschema_ht23.php";
const inputFieldEl = document.getElementById("inputField");
const tableFieldEl = document.getElementById("tableField");
//Event listners


//onload anropa getchannels() och channelPlayer().
window.onload = init;
function init() {
    console.log("initierar...");
    console.log("Hämtar data från JSON...");
    getData();
}

//functions
//Hämta Data från JSON

async function getData() {
  try {
    const response = await fetch(url);
    const data = await response.json();

    renderData(data);
    console.table(data);
  } catch (error) {
    tableFieldEl.innerHTML = "misslyckades att nå data, försök igen senare";
  }
}
//Skriv ut data på skärmen 
async function renderData(data) {
  data.forEach((course) => {
    tableFieldEl.innerHTML += `
   
    <tr>
        <td>${course.code}</td>
        <td>${course.coursename}</td>
        <td>${course.progression}</td>
    </tr>   
    `;
  });
}

inputFieldEl.addEventListener("change", async function searchInput(data){
  console.log("söker..")

});

 

getData();
